.navigation {
  padding: 0;
}

.container {
  padding: 0;
}

@media screen and (min-width: 1200px) {
  .container {
    width: $desktop;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media screen and (min-width: $desktop) {
  .flex-section {
    display: flex;
  }
}
.recording {
  width: $mobile;
  padding-top: 84px;
  padding-bottom: 84px;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center;
  background-color: #191c26;
}
@media (min-width: $tablet) {
  .recording {
    text-align: left;
    width: 725px;

    padding-top: 100px;
    padding-bottom: 100px;
  }
}
@media (min-width: $desktop) {
  .recording {
    width: 655px;
    padding-left: 55px;
    margin-left: 0;
    margin-right: 0;
  }
}
.recording-header {
  color: $whiteTextColor;
  margin-bottom: 0;
  text-align: center;
}
@media (min-width: $tablet) {
  .recording-header {
    text-align: left;
  }
}

.recording-list {
  padding-top: 44px;
  padding-bottom: 44px;
  @extend %list;
}
@media screen and (min-width: $tablet) {
  .recording-list {
    display: flex;
    flex-wrap: wrap;
  }
}
.recording-item {
  margin-bottom: 44px;
}
@media screen and (min-width: $tablet) {
  .recording-item:first-child {
    margin-right: 30px;
  }
}
.recording-item:last-child {
  margin-bottom: 0;
}
.recording-input {
  width: 418px;
  height: 24px;
  color: rgba(157, 164, 189, 0.4);
  background-color: #191c26;
  border-color: transparent;
  border-bottom-color: rgba(157, 164, 189, 0.4);
  padding-bottom: 8px;
}
@media screen and (min-width: $tablet) {
  .recording-input {
    width: 330px;
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: $desktop) {
  .recording-input {
    width: 265px;
  }
}

.recording-input:hover {
  border-bottom-color: $secondaryBgColor;
}
.recording-input::placeholder {
  font-weight: 600;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: rgba(157, 164, 189, 0.4);
}
.recording-input:hover::placeholder {
  color: $whiteTextColor;
}
.recording-textarea {
  height: 76px;
  resize: none;
}
@media screen and (min-width: $tablet) {
  .recording-textarea {
    width: 690px;
  }
}
@media screen and (min-width: $desktop) {
  .recording-textarea {
    width: 560px;
  }
}

.recording-button {
  display: inline-block;
  color: $whiteTextColor;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  background-color: #191c26;
  border: 1px solid #ff6c00;
  border-radius: 25px;
  min-width: 160px;
  height: 40px;
  text-align: center;
}
.recording-button:hover {
  background-color: #ff6c00;
}
.contacts {
  width: $mobile;
  padding-top: 84px;
  padding-bottom: 84px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  font-size: 18px;
  line-height: 1.67;

  color: $primeryAccentTextColor;
  background-color: #191c26;

  background-image: linear-gradient(
      to right,
      rgba(25, 28, 38, 0.5),
      rgba(25, 28, 38, 0.5)
    ),
    url(../images/contacts/mobile.jpg);
}
@media (min-resolution: 2dppx) {
  .contacts {
    background-image: linear-gradient(
        to right,
        rgba(25, 28, 38, 0.5),
        rgba(25, 28, 38, 0.5)
      ),
      url(../images/contacts/mobile@2x.jpg);
  }
}
@media screen and (min-width: $tablet) {
  .contacts {
    width: 725px;

    padding-top: 100px;
    padding-bottom: 100px;
    background-image: linear-gradient(
        to right,
        rgba(25, 28, 38, 0.5),
        rgba(25, 28, 38, 0.5)
      ),
      url(../images/contacts/tablet.jpg);
  }
}
@media (min-resolution: 2dppx) {
  .contacts {
    background-image: linear-gradient(
        to right,
        rgba(25, 28, 38, 0.5),
        rgba(25, 28, 38, 0.5)
      ),
      url(../images/contacts/tablet@2x.jpg);
  }
}
@media (min-width: $desktop) {
  .contacts {
    width: 625px;
    margin-left: 0;
    margin-right: 0;
    padding-left: 100px;

    background-image: linear-gradient(
        to right,
        rgba(25, 28, 38, 0.5),
        rgba(25, 28, 38, 0.5)
      ),
      url(../images/contacts/desktop.jpg);
  }
}
@media (min-resolution: 2dppx) {
  .contacts {
    background-image: linear-gradient(
        to right,
        rgba(25, 28, 38, 0.5),
        rgba(25, 28, 38, 0.5)
      ),
      url(../images/contacts/desktop@2x.jpg);
  }
}
.contacts-header {
  color: $whiteTextColor;
  margin-bottom: 0;
  text-align: center;
}
@media (min-width: $tablet) {
  .contacts-header {
    text-align: left;
  }
}
@media (min-width: $tablet) {
  .contacts-flex {
    display: flex;
  }
}
@media (min-width: $desktop) {
  .contacts-flex {
    display: block;
  }
}
.contacts-address {
  font-style: normal;
}
@media screen and (min-width: $tablet) {
  .contacts-address {
    margin-right: 30px;
  }
}
@media screen and (min-width: $desktop) {
  .contacts-address {
    margin-right: 0px;
    margin-bottom: 70px;
  }
}
.contacts-list {
  width: 418px;
  @extend %list;
  padding-top: 40px;
  padding-bottom: 44px;
}
@media (min-width: $tablet) {
  .contacts-list {
    padding-bottom: 0;
  }
}
@media (min-width: $desktop) {
  .contacts-list {
    width: 270px;
  }
}
.contacts-item {
  margin-bottom: 20px;
}
@media (min-width: $tablet) {
  .contacts-item {
    width: 290px;
  }
}
.contacts-item:last-child {
  margin-bottom: 0;
}
.contacts-link {
  color: inherit;
  @extend%link;
}

.contacts-mail {
  text-decoration: underline;
}
.contacts-icon {
  fill: currentColor;
  margin-right: 16px;
  vertical-align: middle;
}

@media (min-width: $tablet) {
  .contacts-span {
    padding-left: 44px;
  }
}
@media (min-width: $tablet) {
  .contacts-schedule {
    width: 330px;
    margin-left: 30px;
  }
}

.schedule-header {
  color: $whiteTextColor;
}
@media (min-width: $tablet) {
  .schedule-header {
    padding-top: 40px;
  }
}
@media (min-width: $desktop) {
  .schedule-header {
    padding-top: 0;
  }
}
.contacts-text {
  margin: 0;
}
.schedule-header::before {
  background-color: rgba(157, 164, 189, 0.4);
}
